@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.header {
  max-width: 1920px;
  margin: 0 auto;
}
@media screen and (max-width: 1440px) {
  .header {
    padding: 0 30px 0 25px;
  }
}
@media screen and (max-width: 768px) {
  .header {
    padding: 0 25px;
  }
}
@media screen and (max-width: 375px) {
  .header {
    padding: 0 5px 0 3px;
  }
}
.header__body {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 1440.99px) {
  .header__body {
    gap: 60px;
  }
}
@media screen and (max-width: 960px) {
  .header__body {
    grid-template-columns: min-content max-content;
  }
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .header__body {
    gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
.header__column:nth-child(1) {
  grid-column: 1/3;
}
.header__column:nth-child(2) {
  grid-column: 3/4;
}
.header__image {
  width: 40px;
  height: 40px;
}
.header__title {
  font-family: "averta-regular", sans-serif;
  font-size: 19px;
  font-weight: 900;
  line-height: 126.315789%; /* 24px/19 */
  white-space: nowrap;
}
.header__title a {
  transition: color 0.5s ease 0s;
}
.header__title span {
  display: block;
}
.header__title span:nth-child(2) {
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
  text-align: left;
  margin-left: -5px;
}
.header__logo {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 490px) {
  .header__logo {
    margin-right: 3rem;
  }
}
.header__menu {
  white-space: nowrap;
  display: grid;
  grid-template-columns: min-content min-content;
  justify-content: space-between;
}
.header__contacts {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  justify-self: end;
}
.header__phone {
  position: relative;
  text-align: right;
  white-space: nowrap;
  transition: color 0.5s ease 0s;
}
.header__phone a {
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  line-height: 131.25%; /* 21px/16 */
  transition: opacity 0.4s ease-in-out;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .header__phone a {
    font-size: calc(13px + 3 * (100vw - 1440px) / 480);
  }
}
.header__phone a:hover {
  opacity: 0.6;
}
@media screen and (max-width: 490px) {
  .header__phone span {
    display: none;
  }
}
.header__button {
  cursor: pointer;
  margin-left: 2rem;
  width: 35px;
  height: 35px;
  display: none;
}
@media screen and (max-width: 768px) {
  .header__button {
    display: flex;
  }
}

.menu-list__link {
  height: 100%;
  position: relative;
  margin-right: 30px;
  display: inline-block;
  overflow: hidden;
  transition: all 0.4s ease;
}
.menu-list__link span {
  font-size: 16px;
  font-weight: 600;
  position: relative;
  display: block;
  transform: translateY(0);
  opacity: 0.6;
}
.menu-list__link span::before {
  content: attr(data-text);
  position: absolute;
  top: 100%;
  left: 0;
  transition: transform 0.5s ease;
  transform: translateY(0);
  pointer-events: none;
  user-select: none;
  opacity: 1;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .menu-list__link span {
    font-size: calc(14px + 2 * (100vw - 1440px) / 480);
  }
}
.menu-list__link:hover span {
  animation: moveText 0.4s forwards;
  opacity: 1;
}
.menu-list__link:hover span::before {
  font-weight: 600;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .menu-list__link {
    margin-right: calc(20px + 10 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .menu-list__link {
    margin-right: 15px;
  }
  .menu-list__link:last-child {
    margin-right: 35px;
  }
}

@keyframes moveText {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
  }
}